#SliderImage{
    width:100%;
}

#slider-container{

}

.slider-home{
    width: 100%;
    height: 600px;
    display: flex;
}

.slider-span{
    width:500px;
    background-color: black;
}

.slider-backdrop{
    width: 100%;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover; 
}

#slider-movie-detail{
    position:absolute;
    max-width: 600px;
    height: 500px;
    padding: 5%;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

#slider-movie-rating{
    display: flex;
    flex-direction: row;
    align-items: center;
    margin-right: 10px;
}

#slider-movie-rating-score{
    color: #f5b700;
    font-weight: 600;
}

#slider-movie-rating img{
    margin-right: 5px;
}

#slider-movie-genres{
    display: flex;
    flex-direction: row;
    
    padding:0px 5px;
}

#slider-movie-genres p{
    margin-right: 5px;
    border:1px solid white;
    padding: 5px;
    border-radius: 5px;
}

#slider-movie-title{
    font-family: 'Lexend', sans-serif;
    font-weight:600;
    font-size: 46px;
    margin-bottom: 0px;
    text-decoration: none;
    color: white;
}

.slider-movie-infobar{
    display: flex;
    flex-direction: row;
    align-items: center;
}

.slider-movie-infobar p{
    font-family: 'Lexend', sans-serif;
    font-weight:400;
    margin-right: 10px;
    color: #aea7a7;
}

#slider-movie-desc{
    font-family: 'Lexend', sans-serif;
    font-weight: 300;
    line-height: 30px;
}

/* max-width of 800 tablet */

@media only screen and (max-width: 810px) {
   
    .slider-span {
        width: 200px;
        
    }

    
  
}

/* max-width of 420 mobile */

@media only screen and (max-width: 500px) {
   
    .slider-span {
        width: 200px;
        
    }
    
    #slider-movie-detail{
        max-width: 300px;
        top:25%;
    }

    .slider-movie-infobar{
        flex-direction: column;
        align-items: flex-start;
    }
    
    #slider-movie-title{
        font-size: 26px;
    }
    
    #slider-movie-rating{
        margin: 0px;
    }

    .slider-movie-infobar p{
        margin: 5px 0;
        color:#dad7d7;
    }

    #slider-movie-genres{
        padding: 0;
    }

    #slider-movie-genres p{
        border: 1px solid #f5b700;
    }

    #slider-movie-desc{
        line-height: 23px;
        margin: 0;
        font-size: 16px;
        font-weight: 400;
        height: 120px;
        overflow: hidden;
        text-overflow: ellipsis;
    }

}