#searchQuery-SearchBar-container{
    display: flex;
    justify-content: center;
    padding: 2%;
    margin-bottom: 30px;
}

#searchQuery-searchbar{

    width: 50%;
    height: 50px;
    font-size: 25px;
    font-family: 'Lexend', sans-serif;
    border: 3px solid #e4b812;
    border-radius: 5px;
    padding: 0 10px;

}

#searchQuery-SearchResult-container{
    display: flex;
    flex-wrap: wrap;
    padding: 0 5%;
    height: calc(90%);
    
}

.searchQuery-moviecard-wrap{
    margin-right: 10px;
    margin-bottom: 15px;
    width:230px;
}

#searchQuery-noresult{
    display: flex;
    justify-content: center;
    height:700px;
}

#search-noresult-card p{
    font-size: 25px;
    font-family: 'Lexend', sans-serif;
}

