@import url('https://fonts.googleapis.com/css2?family=Lexend:wght@300;400;600;800&display=swap');

#NavBar{

    border: 1px solid #494646;
    display: flex;
    justify-content: space-evenly;

}

#navbar-option {
    display: flex;
    justify-content: center;
    padding-left: 0;
    align-items: center;
}

#navbar-option li{
    padding: 5px;
    list-style: none;
    font-family: 'Lexend', sans-serif;
    font-weight: 600;
}

#navbar-option a{
    padding: 5px;
    list-style: none;
    font-family: 'Lexend', sans-serif;
    font-weight: 600;
}

.nav-active{
    color: #c9a722;
    text-decoration: none;
    font-family: 'Lexend', sans-serif;
    font-weight: 600;
    margin-right: 20px;
}

.nav-active:hover{
    color:#f1b136;
}

.nav-noactive{
    color: white;
    text-decoration: none;
    font-family: 'Lexend', sans-serif;
    font-weight: 600;
    margin-right: 20px;
}

.nav-noactive:hover{
    color:#f1b136;
}


#navbar-searchbar{
    display: flex;
    align-items: center;

}

#navbar-logo{
    display: flex;
    align-items: center;
}