#Movies-container{
    padding: 2%;
}

.movies-section-heading{
    font-family: 'Lexend', sans-serif;
    font-size: 32px;
}


.movie-card-container{

    display: flex;
    flex-wrap: wrap;
}

.movie-card{
 
    width: 220px;
    margin-right: 5px;
    margin-bottom: 15px;
   
}

.movie-card-img-wrap{
    width: 220px;
    height: 330px;
    overflow: hidden;
}

.movie-card-img{
    transition: transform .5s ease,-webkit-transform .5s ease;
}

.movie-card-img:hover{
    transform: scale(1.1);
}



.movie-card-title{
    text-overflow: ellipsis;
    font-family: 'Lexend', sans-serif;
    font-weight: 400;
    margin-top:5px;
    margin-bottom: 5px;
    text-decoration: none;
    color: white;
}

.movie-card-ratingInfo{
    display: flex;
    flex-direction: row;
    align-items: center; 
    height: 25px;
    margin-top: 5px;
}

.movie-card-ratingInfo img{
    margin-right: 5px;
}


.movie-card-ratingInfo-vote{
    font-family: 'Lexend', sans-serif;
    color: #f5b700;
    font-weight: 600;
}

/* max-width of 1377* 720p desktop */

@media only screen and (max-width: 1377px) {
    
    .movie-card{
        width: 205px;
    }

    .movie-card-img-wrap{
        width: 200px;
        height: 300px;
    }

    .movie-card-img {
        
        width: 200px;
    }

    #Movies-container {
        padding: 4%;
    }
  
}

/* max-width of 800 tablet */

@media only screen and (max-width: 810px) {
    
    .movie-card {
        width: 180px;
    }

    .movie-card-img-wrap {
        width: 180px;
        height: 270px;
    }

    .movie-card-img {
        
        width: 175px;
    }
  
}

/* max-width of 450 mobile */

@media only screen and (max-width: 450px) {
    
    .movies-section-heading {
      
        font-size: 26px;
    }

    .movie-card {
        width: 175px;
    }

    .movie-card-img-wrap {
        width: 180px;
        height: 270px;
    }

    .movie-card-img {
        
        width: 175px;
    }
  
}

@media only screen and (max-width: 370px) {
    
    .movies-section-heading {
      
        font-size: 26px;
    }

    .movie-card {
        width: 150px;
    }

    .movie-card-img-wrap {
        width: 160px;
        height: 225px;
    }

    .movie-card-img {
        
        width: 150px;
    }
  
}