#single-movie-slider{
    height:800px;
    background-position: center;
}

#singlemovie-back-button{
    background: transparent;
    color: white;
    padding: 10px;
    font-family: 'Lexend', sans-serif;
    border: 1px solid white;
    position: absolute;
    top: 15%;
    left: 8%;
    border-radius: 5px;
    cursor: pointer;
}

#singlemovie-back-button:hover{
    background-color: #f5b700;
    color: black;
}


#singlemovie-detail-container{
    display: flex;
    flex-direction: row;
    position: relative;
    top: 40%;
    margin: 0 20%;
    font-family: 'Lexend', sans-serif;
}

#singlemovie-poster img{
    border: 1px solid white;
}

#singlemovie-title{
    margin: 0;
    font-family: 'Lexend', sans-serif;
    font-size: 32px;
    font-weight: 600;
}

#singlemovie-tagline{
    margin: 0px;
    color:#ded3d3;  
}

#singlemovie-moviedetails{

    margin-left: 15px;
    width: 800px;
}

#singlemovie-rating{
    
    display: flex;
    align-items: baseline
    
}

#singlemovie-rating img{
    margin-right: 5px;
}

#singlemovie-genre-wrapper{
    display: flex;    
}

.singlemovie-genre{
    padding: 5px;
    border: 1px solid #f5b700;
    border-radius: 5px;
    margin-left: 5px;
}

#singlemovie-desc{
    line-height: 30px;
    margin-top: 0;
    border-left: 1px solid white;
    padding-left:10px;
}

#singlemovie-runtime{
    margin-left: 10px;
}

#singlemovie-cast-image-container{

    display: flex;
    flex-wrap: wrap;
    font-family: 'Lexend', sans-serif;

}

#singlemovie-cast-container{
    padding: 0 5%;
}

.singlemovie-heading{
    font-family: 'Lexend', sans-serif;
    font-size: 32px;
}

.singlemovie-cast-card{
    width: 155px;
}

.singlemovie-cast-card img{
   width: 138px; 
}

.singlemovie-castcard-character{
    margin: 0;
    font-weight: 600;
    width: 140px;
}

.singlemovie-castcard-name{
    margin-top: 5px;
    color: #d3cccc;
    width: 140px;
}

#singlemovie-trailer-container{
    padding: 0 5%;
}

#singlemovie-related-container{
    display: flex;
    flex-direction: column;
    padding: 0 5%;
  
}

#singlemovie-related-result-wrap{
    display: flex;
    flex-wrap: wrap;
     
}

.singlemovie-related-moviecard{
    display: flex;
    flex-direction: column;
    width: 220px;
    margin: 0 10px 15px 0;
}

.singlemovie-related-movieTitle{
    font-family: 'Lexend', sans-serif;
    font-size: 18px;
    color: white;
    text-decoration: none;
    font-weight: 600;

}

/* max-width of 800 tablet */

@media only screen and (max-width: 810px) {
    
    #singlemovie-detail-container{
        margin: 0 10%;
    }

    .singlemovie-genre{
        text-align: center;
        padding: 3px;
    }

    #single-movie-slider{
        background-repeat: no-repeat;
        background-size: cover;
    }
    
    #singlemovie-moviedetails{
        height: 480px;
        overflow: scroll;
    }

}

/* max-width of 360 mobile */

@media only screen and (max-width: 460px) {
    
    #single-movie-slider{
        height: 430px;
        background-position: center;
        background-repeat: no-repeat;
    }

    #singlemovie-detail-container{
        top:50%;
    }

    #singlemovie-poster img{
        width: 100px;
    }

    .singlemovie-related-moviecard{
        width: 150px;
    }

    .singlemovie-related-moviecard-img{
        width:150px;
    }

    #singlemovie-desc{
        display: none;
    }

    #singlemovie-rating{
        display: none;
    }

    #singlemovie-releasedate,#singlemovie-director,#singlemovie-music-director{
        display: none;
    }

    #singlemovie-moviedetails{
        width:325px;
    }

    #singlemovie-desc-mobile{
        line-height: 30px;
        margin-top: 0;
        border-left: 1px solid white;
        padding-left: 10px;
        font-family: 'Lexend', sans-serif;
        
    }

    #singlemovie-rating-mobile{
     
        display: flex;
        align-items: baseline;
        
    }

    #singlemovie-genre-wrapper-mobile{
        display: flex;
    }

    .singlemovie-genre{
        padding: 5px;
    }

    #singlemovie-runtime-mobile{
        margin-left: 10px;
    }

    #singlemovie-mobile-detail-container p{
        font-family: 'Lexend', sans-serif;    
    }

    #singlemovie-releasedate-mobile{
        font-family: 'Lexend', sans-serif;
    }

    #singlemovie-director-mobile{
        font-family: 'Lexend', sans-serif;
    }

    #singlemovie-music-director-mobile{
        font-family: 'Lexend', sans-serif;
    }
  
}

